@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Birthstone&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Helvetica Neue', 'Noto Sans','Verdana', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
  background: url(../../static/media/main.fa58bf9e.png);
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.component {
  /*background-color: rgba(0, 0, 200, 0.4);*/
  height: 100vh;
  width: 100vw;
}



.result {
	display: flex;
	/*margin: 3vh 10vw;*/
	padding: 3vh 10vw;
	/*gap: 1rem;*/
	align-items: center;
	/*border: 1px solid red;*/
}

.result__img-wrapper {
	/*border: 1px solid red;*/
	height: calc(6vh + 2rem);
	width:  calc(6vh + 2rem);
	position: relative;
}

.result__img {
	height: calc(5vh + 2rem);
	max-width: calc(6vh + 2rem);
	margin-right: 1rem;
	position: absolute;
	bottom: 0;

}

.result__statistic {
	width: 61vw;
	/*padding: 1vh 0;*/
	/*border: 1px solid red;*/
}

.result__text {
	display: flex;
	justify-content: space-between;
	margin-bottom: 0.5rem;
	color: white;
}

.total-voter {
	color: #dff;
	font-size: 0.8rem;
	margin-right: 0.5rem;
}

.result__number {
	display: flex;
	grid-gap: 10px;
	gap: 10px;
	align-items: baseline;
}

.result__bar {
	/*border: 1px solid red;*/
	background-color: #888;
	height: 5vh;
	width: 61vw;
	border-radius: 5px;
}

